
*{
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
    margin: 0;
}

nav{
    background: rgb(4, 42, 92);
    width: 100vw;
    height: 70px;
    display: flex;
    /* border: 12px solid black; */
}

nav a{
    text-decoration: none;
    color: white;
    font-size: 30px;
    font-weight: 400;
    font-family: cursive;
}



body
{
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(8, 12, 58);
}

.container
{
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 10px solid rgb(97, 94, 94);
    background: black;
    border-radius: 5px;
}

.buttons
{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(65px, auto);
}

input[type="text"]
{
    height: 70px; width: 245px;
    background-color: black;
    border: 3px solid rgb(0, 0, 0);
    color: white;
    text-align: right;
    font-size: 30px;
    font-weight: 600;
}

#clear
{
    grid-column: 1/3;
    grid-row: 1;
    background-color: rgb(216, 77, 77); 
}

#clear:hover
{
    background-color: rgb(228, 88, 88);
}

#equalto
{
    grid-column: 3/5;
    grid-row: 5;
}

#equalto:hover
{
    background-color: rgb(65, 248, 9);
}

#backspace
{
    color: white;
}

button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    background:  #262834;;
}

button:hover
{
    background-color: rgb(161, 152, 152);
}

.standout
{
    background-color: rgb(26, 153, 192);
}

.standout:hover
{
    background-color: rgb(55, 223, 245);
}